*{
  padding: 0px;
  margin: 0px;
}

.App {
  height: 600px;
  width: 600px;
}

img{
  pointer-events: none;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  outline: none;
}

.Header{
  display: flex;
  align-items: center;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  justify-content: space-between;
}

.Header p{
  font-size: 1.3rem;
  font-weight: 700;
}

.backarrow{
  height: 100%;
  padding: 0px 10px;
}

.forwardarrow{
  height: 100%;
  padding: 0px 10px;
}